import Image1 from './post1.png'
import Image2 from './post2.png'
import Image3 from './post3.jpg'
import Image4 from './post4.jpg'
import FbImageLibrary from 'react-fb-image-grid'


function PostImage (){
    return(
            <FbImageLibrary  images = {[Image1,Image2,Image3,Image4] }/>
  
    )
}
export default PostImage;